.slick-slide > div > div:focus {
	outline: none;
}

.slick-slider {
	box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
		0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
	margin: 0 -10px;
}
.slick-track {
	margin-top: 20px;
}
.magic-dots ul .slick-active button {
	/* color: black; */
	/* more CSS */
}
.slick-next:before,
.slick-prev:before {
	color: #000;
}

@media only screen and (max-width: 599px) {
	.slick-slider {
		display: none;
	}
}
